[data-part="global-sidebar-container"] {
  h2,
  h3,
  h4,
  p,
  div {
    font-family: "Raleway";
  }
  h1 {
    font-family: "Berkshire Swash", handwriting;

    //hello
  }
}
.main-header-bar-wrap {
  display: none;
}
svg {
  width: 100%;
}
.nav-link {
  svg {
    max-height: 1em;
  }
}
button,
a {
  svg {
    width: 1em;
    fill: currentColor;
  }
}

//box-shadow
.shadow {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}
.shadow-md {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
}
.shadow-sm {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
//--/--/--/--/--/--/--/--/--/--/--/--/--/--/--/--/--//
.admin-bar-space {
  @media (min-width: 650px) {
    margin-top: 46px;
  }
  @media (min-width: 783px) {
    margin-top: 32px;
  }
}
//

.button-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  button + button,
  a + a {
    margin-left: 10px;
  }
}
